// Colors
$white: #fff;
$black: #000;
$lightOrange: #ffc500;
$darkOrange: #fb6c1f;
$orange: #ffb100;
$gray: rgba(0, 0, 0, 0.7);
$lightgGray: #ececec;

//box shadow
$box-shadow: 0 1px 1px rgba(0, 0, 0, 0.11), 0 2px 2px rgba(0, 0, 0, 0.11),
